/******************************************************************************
 * App.js
 *****************************************************************************/

/*************************************************
 * import
 *************************************************/
import React from "react";
import { Routes, Route, Link } from "react-router-dom";

import Top from "./components/pages/top/Top";
import Artworks from "./components/pages/Artworks/Artworks";
import Otherworks from "./components/pages/Otherworks";
import About from "./components/pages/About";
import Contact from "./components/pages/Contact/Contact";
import Header from './components/pages/common/Header'
import Footer from './components/pages/common/Footer'

/*************************************************
 * App
 *************************************************/
function App() {
  return (
    <>
      <Header />
      <Routes>
        <Route path="/portfolio-client-app/" element={<Top />} />
        <Route path="/portfolio-client-app/artworks" element={<Artworks />} />
        <Route path="/portfolio-client-app/otherworks" element={<Otherworks />} />
        <Route path="/portfolio-client-app/about" element={<About />} />
        <Route path="/portfolio-client-app/contact" element={<Contact />} />
        <Route path="*" element={<Top />} />
      </Routes>
      <Footer />
    </>
  )
}
export default App;
